<template>
<div>
    <Input :type='show?"text":"password"' :size="size" v-model.trim='inputvalue' :placeholder='placeholder'>
        <i slot="suffix" :title="show?'隐藏密码':'显示密码'" @click="changePass" style="cursor:pointer;" class="iconfont" :class="show?'mychat-icon-password-hidden':'mychat-icon-password-show'"></i>
    </Input>
</div>
</template>

<script>
export default {
    props: {
        value: {
            default: "",
            type: String,
            required:true
        },
        placeholder: {
            default: "",
            type: String
        },
        size:{
            default: "default",
            type: String
        }
    },
    data(){
        return{
            inputvalue:"",
            show:false,
        }
    },
    watch:{
        inputvalue(news,olds){
            this.$emit("input",news);
            this.$emit("update:value",news);
        },
        value(news){
            this.inputvalue=news;
        }
    },
    mounted(){
        this.inputvalue=this.value;
    },
    methods:{
        changePass(){
            this.show=!this.show;
        }
    }
}
</script>

